import {useSelector} from 'react-redux'
import {Typography} from '@mui/material'


const Info = () => {
  const links = useSelector(({Links}) => Links)

  return(
    <div>
      <Typography variant="h6" component="h2">
        : {links.platform}
      </Typography>
      <Typography variant="h6" component="h2">
         : {links.deeplink}
      </Typography>
      <Typography variant="h6" component="h2">
        : {links.screen}
      </Typography>
      <Typography variant="h6" component="h2">
         : {links.internalSource}
      </Typography>
      <Typography variant="h6" component="h2">
         : {links.externalSource}
      </Typography>
    </div>
  )
}

export default Info;
